@import './_variable';

// guide
h1, h2, h3, h4, h5, h6 {
  font-family: 'NotoKR-md'; }
h1 {
  font-size: 28px; }
h2 {
  font-size: 22px; }
h3 {
  font-size: 20px; }
h4 {
  font-size: 17px; }
h5 {
  font-size: 15px; }
h6 {
  font-size: $basic-size; }

p, span, a, ul, li, ol, dl, dt, dd, pre, label, input, table, caption, th, td, select, textarea {
  font-family: 'NotoKR'; }

p {
  font-size: $basic-size;
  line-height: 150%;
  color: $text-color; }

em {
  font-size: 15px; }

a {
  display: block;
  font-size: $basic-size;
  text-align: center;
  letter-spacing: 0.1em;
  color: $link-color;
  text-decoration: none;
  &:hover, &:focus, &:active {
    color: $link-color;
    text-decoration: none; } }

li {
  list-style: none;
  color: $text-color;
  letter-spacing: -0.024em;
  line-height: 150%; }

ol li {
  list-style: inherit; }

ol {
  padding-left: 15px; }

.boldy {
  font-weight: bold; }
.hypery {
  color: $link-color; }

.btn_normal.hover {
  background-color: #78909c;
  color: $white; }

.btn_normal {
  font-family: 'NotoKR-md';
  text-align: center;
  color: $btn-normal-color;
  border: 2px solid $btn-normal-color;
  border-radius: 4px;
  width: 87px;
  height: 36px;
  line-height: 36px;
  transition: all .3s $btn-bezier;
  &:hover {
    background-color: $btn-noraml-hover-color;
    color: $white;
    border: 2px solid transparent; }
  &:active {
    background-color: $btn-normal-active-color;
    color: $white;
    border: 2px solid transparent; } }
.btn_disabled {
  background-color: $btn-normal-disabled-color;
  border-radius: 4px;
  color: #000;
  width: 87px;
  height: 36px;
  line-height: 36px;
  &:hover, &:focus, &:active {
    color: #000; } }

a.btn_normal.on {
  color: $link-color;
  border: 2px solid $link-on-hover-color;
  transition: all .3s $btn-bezier;
  &:hover {
    border: 2px solid transparent;
    background-color: $link-on-hover-color;
    color: $white;
    box-shadow: 0px 2px 8px lighten($text-color, 30%); }
  &:active {
    background-color: $link-on-color;
    color: $white; } }

.btn_lg {
  width: 280px;
  height: 60px;
  line-height: 55px;
  font-size: 17px; }

.btn_md {
  width: 220px;
  height: 50px;
  line-height: 45px;
  font-size: 15px; }

.btn_sm {
  width: 140px;
  height: 40px;
  line-height: 35px;
  font-size: 15px; }

.list {
  border: {
    top: 1px solid $list-bd-color; }
  li {
    border-bottom: 1px solid $list-bd-color;
    margin-left: 15px;
    padding: {
      top: 15px;
      bottom: 15px; }
    &:last-child {
      border-bottom: none; } } }

ul, ol, dl {
  border-bottom: 1px solid $list-bd-color; }

ol.list li {
  list-style-position: inside;
  margin-left: 15px;
  padding-left: 0; }

dl.list {
  border: none; }
dl.list {
  dt {
    font-family: 'NotoKR-md';
    background-color: #f9f9f9;
    height: 25px;
    line-height: 25px;
    padding-left: 15px; }
  dd {
    border-bottom: 1px solid $list-bd-color;
    padding: {
      top: 12px;
      bottom: 12px; } } }

label {
  font-size: $basic-size;
  color: $text-color; }

.form_box {
  width: 305px; }
input, select {
  border: 1px solid #bbb;
  border-radius: 4px;
  font-size: $basic-size;
  color: $text-color;
  width: 100%;
  height: 35px;
  padding-left: 5px; }

.srch_box {
  position: relative;
  input {
    padding-left: 32px; }
  .ic_deco {
    position: absolute;
    top: 2px;
    left: 10px;
    font-size: 20px;
    width: 13px;
    height: 13px;
    color: #686871; }
  .del {
    position: absolute;
    top: 8px;
    right: 11px;
    font-size: 22px;
    color: #8e8e93;
    width: 20px;
    height: 20px;
    line-height: 20px; } }

textarea {
  border-radius: 4px;
  width: 100%;
  height: 100px;
  resize: none; }

pre {
  width: 100%;
  height: 100px;
  background-color: #FFF;
  border-radius: 4px;
  white-space: pre;
  word-break: break-all;
  word-wrap: break-word; }

.table {
  width: 100%;
  th {
    font-size: 14px;
    text-align: center; }
  td {
    font-size: 14px; } }

.table tr:last-child {
  border-bottom: 1px solid #ddd; }

.sd_table thead th {
  background-color: #aaa;
  color: #fff;
  border-right: 1px solid #ddd;
  &:last-child {
    border-right: none; } }

.sd_table tbody td {
  font-size: 14px;
  border-right: 1px solid #ddd;
  &:first-child {
    border-left: 1px solid #ddd; } }

.icon .icon_list a {
  display: inline-block;
  vertical-align: middle;
  margin-right: 50px; }

.left_arrow {
  font-size: 42px;
  width: 18px;
  height: 30px;
  line-height: 30px;
  color: #686871;
  .fa {
    line-height: 30px; }
  &:hover, &:focus, &:active {
    color: #686871; } }

.left_arrow .ic_deco {
  margin-top: -5px; }

.menu {
  display: inline-block;
  width: 23px;
  height: 20px;
  line-height: 20px; }

.nav {
  background-color: #686871;
  position: relative;
  width: 100%;
  height: 4px;
  &:before, &:after {
    position: absolute;
    left: 0;
    content: '';
    background-color: #686871;
    width: 100%;
    height: 4px; }
  &:before {
    top: -8px; }
  &:after {
    top: 8px; } }

.logout {
  width: 44px;
  height: 34px; }
.logout span {
  display: block;
  font-size: 16px;
  text-align: center;
  color: #333;
  width: 100%; }
.logout .txt {
  font-size: 12px;
  letter-spacing: -0.04em;
  color: #686871; }

.fa {
  color: #8e8e93; }

.glyphicon-star {
  font-size: 25px; }

.on .glyphicon-star {
  color: #ffb400; }


.carousel {
  position: relative;
  .carousel_btn {
    position: absolute;
    top: 33%;
    width: 100%;
    a {
      position: absolute;
      top: 0;
      font-size: 36px;
      &.prev {
        left: 5px; }
      &.next {
        right: 5px; }
      .fa {
        color: #fff; } } } }

.circle {
  display: inline-block;
  background-color: transparent;
  border: 2px solid #fff;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  margin-right: 10px; }

.on {
  .circle {
    background-color: #FFF;
    border: 2px solid transparent;
    width: 20px;
    border-radius: 6px; } }

.carousel {
  .indicator {
    position: absolute;
    bottom: 0;
    text-align: center;
    width: 100%;
    .bullet_list {
      display: inline-block; } } }
